module.exports = "<section><p>For Christmas this year I received an Amazon Echo Dot. I have never been really interested in voice assistants, but having received one for free I thought I might as well check it out. While I never really cared to control the Hue bulbs and smart outlets I have scattered throughout my apartment, I quickly realized multi-room audio would be the biggest advantage of these devices for me. Rather than spending a ton of money on something like Sonos, I can very cheaply set up a whole-home audio system. While the audio quality on these little devices will never be as good as Sonos, I find it adequate (especially for the price). Plus, in my main listening area, my living room, I have a home theater sound system that I can tie into. That should be easy enough, right?</p>\n<h4>The Problem</h4>\n<p>Amazon supports two ways to connect an Echo to an external speaker system:</p>\n<ol>\n<li>Via bluetooth</li>\n<li>Via an aux cable</li>\n</ol>\n<p>The obvious choice to connect to a home theater receiver would be via aux. It offers the best quality with no latency or fiddling with wireless connections. The major downside of this is that ALL of the Echo's audio will be routed through the line out port. You cannot, for example, configure music to route to the aux while voice responses come through the internal speaker. You also can't disable and enable the output using your voice. As long as something is plugged in, the internal speaker is fully disabled. This is a <strong>huge</strong> problem for a home theater system. If I plug the Echo into it using an aux cable:</p>\n<ul>\n<li>I have to leave the receiver on at all times</li>\n<li>I cannot use Alexa while watching TV or listening to music on another source</li>\n<li>I have to always turn the receiver back to the Echo input when I'm done watching TV</li>\n</ul>\n<p>This set up would make the whole system much more complicated and unintuitive, which is a huge turn-off for me.</p>\n<p>A bluetooth connection on the Echo is a little bit better. While you cannot choose which audio to route through which output, you <em>can</em> use your voice to connect and disconnect to the speaker. That way, you are in control of where the sound is output to. However, it still requires a manual step. Further, my receiver does not have built-in bluetooth.</p>\n<p>With the smart devices I have in my home, it would be great if I could control the output of the Echo in an easy and automated way.</p>\n<h4>The Solution</h4>\n<p>The solution I have settled on is somewhat convoluted, but works very well. It involves the following parts:</p>\n<ol>\n<li>Some sort of stereo system (a home theater receiver in my case)</li>\n<li>A smart outlet (I'm using a TP-Link model)</li>\n<li>A Harmony Hub and Remote</li>\n<li><a href=\"https://www.home-assistant.io/\">Home Assistant</a></li>\n<li>A bluetooth receiver that runs <strong>only</strong> on wall power (not battery-powered or rechargable)</li>\n<li>An Amazon Echo</li>\n</ol>\n<p>The main idea is that the bluetooth receiver is powered via the smart outlet. In turn, the outlet is controlled by the Harmony Hub. By setting up the Harmony to only turn on the bluetooth outlet when the receiver is on the proper source, this allows us to easily control what device the Echo plays sound through.</p>\n<p>If the receiver is off or I'm playing something through a different source, I am able to use the Echo as normal. I can also play multi-room audio if I don't care about having it run through the stereo system. When I want the Echo to output to the stereo, I simply press a single button on the Harmony remote (or use a voice command) which does the following:</p>\n<ol>\n<li>Turns the receiver on</li>\n<li>Turns the smart outlet on, which turns the bluetooth receiver on\n<ul>\n<li>The Echo automatically connects to this and switches its audio output</li>\n</ul>\n</li>\n<li>Sets the receiver to the proper input</li>\n</ol>\n<p>When I'm done listening to music on the stereo, I press the &quot;off&quot; button on the remote, everything shuts off, and the Echo switches back to the internal speaker.</p>\n<h4>Setup</h4>\n<p>Every component is easy to set up on it's own. The problems arise when you try to get everything to talk to each other. This really made me realise what a mess the home automation industry is at the moment. Let's break down the pieces of communication that need to happen for this project:</p>\n<table>\n<thead>\n<tr>\n<th>Communication</th>\n<th>Supported?</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>Bluetooth Receiver  Amazon Echo</td>\n<td></td>\n</tr>\n<tr>\n<td>Amazon Echo  Harmony Hub</td>\n<td></td>\n</tr>\n<tr>\n<td>Harmony Hub  TP-Link Smart Outlet</td>\n<td></td>\n</tr>\n</tbody>\n</table>\n<p>As you can see, the Harmony Hub cannot natively control WiFi devices. It requires a hub (like SmartThings) to facilitate these communications.</p>\n<h5>Home Assistant</h5>\n<p>To allow Harmony to control the WiFi outlet I used Home Assistant to emulate a hue bridge. This presents a fake Hue bridge on the network that supports non-Hue devices. The Harmony is able to control this because it just looks like a Hue device.</p>\n<p>To get started you'll need to run Home Assistant on a computer in your home. There are a ton of guides out there for different systems so I'm not going to go into depth. I have mine running on a Raspberry Pi using the <a href=\"https://hub.docker.com/r/homeassistant/raspberrypi3-homeassistant/\"><code>homeassistant/raspberrypi3-homeassistant</code></a> Docker image.</p>\n<p>It's a really good idea to give both the computer that is running Home Assistant, and the devices you are connecting to, a static IP address. Otherwise, your Home Assistant configuration can break when your router assigns a new IP to your devices.</p>\n<p>Configurating Home Assistant is done mainly through <code>yaml</code> configuration files. There are a few types of devices, such as Philips Hue, that can automatically be discovered and configured, but the rest is done through via yaml. Find the configuration file for your version of Home Assistant (usually called <code>configuration.yaml</code>) and add the following:</p>\n<pre class=\"hljs\"><div class=\"code\"><span class=\"hljs-attr\">switch:</span>\n<span class=\"hljs-attr\">  - platform:</span> <span class=\"hljs-string\">tplink</span>    <span class=\"hljs-comment\"># if you have a different brand, you will need to find the proper platform ID</span>\n<span class=\"hljs-attr\">    host:</span> <span class=\"hljs-number\">172.16</span><span class=\"hljs-number\">.1</span><span class=\"hljs-number\">.102</span>  <span class=\"hljs-comment\"># the static IP address of the outlet</span>\n<span class=\"hljs-attr\">    name:</span> <span class=\"hljs-string\">bluetooth_outlet</span>\n</div></pre>\n<p>After reloading your configuration, you should be able to control the outlet via the Home Assistant UI.</p>\n<p>Next, we need to create a Hue Bridge to expose to Harmony. Again, edit the configuration with the following:</p>\n<pre class=\"hljs\"><div class=\"code\"><span class=\"hljs-attr\">emulated_hue:</span>\n<span class=\"hljs-attr\">    expose_by_default:</span> <span class=\"hljs-literal\">true</span>\n<span class=\"hljs-attr\">    entities:</span>\n        <span class=\"hljs-string\">switch.bluetooth_outlet:</span>\n<span class=\"hljs-attr\">            name:</span> <span class=\"hljs-string\">\"Bluetooth Outlet\"</span>\n</div></pre>\n<p>And that should do it! All this section does is turn on the emulated bridge and expose a single entity through it.</p>\n<h5>Harmony Hub Setup</h5>\n<p>To set up your Harmony Hub to work with the emulated Hue bridge, do the following:</p>\n<ol>\n<li>Open your Harmony app</li>\n<li>Tap the menu icon, then &quot;Harmony Setup&quot;</li>\n<li>Choose &quot;Add/Edit Devices &amp; Activities&quot;</li>\n<li>Choose &quot;Devices&quot;</li>\n<li>If you currently have a Hue bridge connected to your Harmony, you must delete it\n<ul>\n<li>If you have lights you still want to control using your Harmony remote, take a look at the <a href=\"#using-hue-lights-with-harmony\">Using Hue Lights with Harmony</a> section before proceeding</li>\n</ul>\n</li>\n<li>Unplug any physical Hue bridges you may have</li>\n<li>Click &quot;Add Device&quot;, then choose &quot;Philips Hue&quot; under &quot;Home Control&quot;</li>\n<li>When you get to the step that tells you to press the center button, wait for a couple minutes. Home Assitant should automatically send the command to register it</li>\n<li>Complete the setup</li>\n<li>Plug your physical Hue bridges back in</li>\n</ol>\n<p>You should now be able to set up activities and home control buttons to use your smart outlet, just like this:</p>\n<table>\n<thead>\n<tr>\n<th>Activity</th>\n<th>Home Control Button</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td><img src=\"" + __webpack_require__(180) + "\" alt=\"\"></td>\n<td><img src=\"" + __webpack_require__(181) + "\" alt=\"\"></td>\n</tr>\n</tbody>\n</table>\n<p><a name=\"using-hue-lights-with-harmony\"></a></p>\n<h5>Using Hue Lights with Harmony</h5>\n<p>The one remaining issue with this solution is the ability to control your Hue lights with the Harmony, since we removed the physical bridge from the Harmony's configuration. Harmony can, unfortunately, only connect to one Hue bridge at the time, so we will have to find a way to expose the lights through the emulated bridge.</p>\n<p>My home has Hue lights in the Living Room and Bedroom. I only care about controling the Living Room lights with the Harmony because that's the room the remote is in. To add the lights to the emulated bridge, we need to connect the physical bridge to Home Assistant. Hue Bridges can be added automatically using the UI:</p>\n<ol>\n<li>Navigate to the UI in a web browser (usually port 8123 of the device it's running on)</li>\n<li>Click &quot;Configuration&quot; &gt; &quot;Integrations&quot;</li>\n<li>Under &quot;Set up a new integration&quot;, choose &quot;Configure&quot; next to &quot;Philips Hue&quot;</li>\n<li>Follow the instructions</li>\n</ol>\n<p>To expose your lights on the emulated bridge, add them to the <code>emulated_hue.entities</code> section of your yaml file:</p>\n<pre class=\"hljs\"><div class=\"code\"><span class=\"hljs-attr\">emulated_hue:</span>\n<span class=\"hljs-attr\">    expose_by_default:</span> <span class=\"hljs-literal\">true</span>\n<span class=\"hljs-attr\">    entities:</span>\n        <span class=\"hljs-string\">light.living_room:</span>\n<span class=\"hljs-attr\">            name:</span> <span class=\"hljs-string\">\"Living Room Light\"</span>\n        <span class=\"hljs-string\">switch.bluetooth_outlet:</span>\n<span class=\"hljs-attr\">            name:</span> <span class=\"hljs-string\">\"Bluetooth Outlet\"</span>\n</div></pre>\n<p>These entities should now be available for use in your Harmony app!</p>\n<hr>\n<p>I hope this was a helpful guide to integrating your Amazon Echo with your stereo or home theater sound system. Here are links to some of the products I have used. Note these are NOT affiliate links. However, they are setup to redirect to your local Amazon page using <a href=\"https://affiliate-geo-target.com\">A-FWD</a>.</p>\n<table>\n<thead>\n<tr>\n<th>Product</th>\n<th>Link</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>Amazon Echo</td>\n<td><a href=\"http://a-fwd.com/asin-ca=B0792JYXZK\">Amazon Echo Dot</a></td>\n</tr>\n<tr>\n<td>Bluetooth Receiver</td>\n<td><a href=\"http://a-fwd.com/asin-ca=B016NUTG5K\">Esinkin Wireless Audio Adapter</a></td>\n</tr>\n<tr>\n<td>Smart Outlet</td>\n<td><a href=\"http://a-fwd.com/asin-ca=B01K1JVZOE\">Kasa Smart Plug Mini Outlet by TP-Link</a></td>\n</tr>\n<tr>\n<td>Amazon Echo</td>\n<td><a href=\"http://a-fwd.com/asin-ca=B0792JYXZK\">Amazon Echo Dot</a></td>\n</tr>\n<tr>\n<td>Harmony Hub</td>\n<td><a href=\"http://a-fwd.com/asin-ca=B00N3RFC4G\">Logitech Harmony Companion</a></td>\n</tr>\n</tbody>\n</table>\n</section>\n";

