module.exports = "<section><p>Single-Page Apps (SPAs) are all the rage right now.  They make it extremely easy to build feature-rich, highly performant web applications.  So you go ahead and build yourself a SPA, but one question remains.  Where do you host it?</p>\n<p>This article is separated into two parts.  First, an outline of the different SPA hosting methods I have used, along with their pros and cons.  Second, my experience with hosting an SPA on GitHub Pages, including any trouble I have had.  I hope to help the readers make an informed decision regarding where to host their app, and if they do choose to use any of the methods I talk about, learn from my mistakes.</p>\n<p>If you wish to focus on GitHub Pages, and skip the other methods, you can jump to section 4: GitHub Pages.</p>\n<h4>1. Google App Engine</h4>\n<p>I didn't really get into web development until I started my current job.  Yes, I knew some HTML and CSS, and I had taken a university web dev class.  However, I wasn't all that interested until I started using those skills in the workplace.</p>\n<p>At my job, all of our core applications are built using App Engine.  We use the Python version, with Jinja2 as our templating language, and Knockout.js for frontend code.  After a while I began to feel very comfortable developing in App Engine.  It was familiar, and I had gotten to the point where I could throw together a new application relatively quickly and easily.</p>\n<p>Now, back to SPAs.  In the summer of 2016, I started working on <a href=\"https://www.rolluptracker.com/\">Roll Up Tracker</a>.  Roll Up Tracker is an Angular2 web-app that allows you to track your wins and losses during the Roll Up The Rim To Win season at Tim Hortons.  Naturally, when I started the project I decided to host it on App Engine.  It might seem a bit odd to have to bootstrap an entire App Engine project just to serve some HTML and Javascript.  However, I justify it by the fact that the backend was written in Python, using Google Cloud Datastore as the database.  It made sense to keep the frontend hosted alongside that.</p>\n<p>The implementation was pretty straightforward.</p>\n<ol>\n<li>Add a couple rules for the frontend files, and a backend catch-all to your <code>app.yaml</code></li>\n<li>Create a wildcard route to a handler that just serves <code>index.html</code></li>\n<li>Create your API routes above your wildcard so they get matched first</li>\n</ol>\n<pre class=\"hljs\"><div class=\"code\"><span class=\"hljs-comment\"># app.yaml</span>\n<span class=\"hljs-attr\">- url:</span> <span class=\"hljs-string\">/assets/(.*)</span>\n<span class=\"hljs-attr\">  static_files:</span> <span class=\"hljs-string\">dist/assets/\\1</span>\n<span class=\"hljs-attr\">  upload:</span> <span class=\"hljs-string\">dist/assets/.*</span>\n\n<span class=\"hljs-attr\">- url:</span> <span class=\"hljs-string\">/(.*\\.(js|map))$</span>\n<span class=\"hljs-attr\">  static_files:</span> <span class=\"hljs-string\">dist/\\1</span>\n<span class=\"hljs-attr\">  upload:</span> <span class=\"hljs-string\">dist/(.*\\.(js|map))</span>\n\n<span class=\"hljs-attr\">- url:</span> <span class=\"hljs-string\">.*</span>\n<span class=\"hljs-attr\">  script:</span> <span class=\"hljs-string\">main.app</span>\n<span class=\"hljs-attr\">  secure:</span> <span class=\"hljs-string\">always</span>\n</div></pre>\n<pre class=\"hljs\"><div class=\"code\"><span class=\"hljs-class\"><span class=\"hljs-keyword\">class</span> <span class=\"hljs-title\">MainHandler</span><span class=\"hljs-params\">(BaseHandler)</span>:</span>\n\n    <span class=\"hljs-function\"><span class=\"hljs-keyword\">def</span> <span class=\"hljs-title\">get</span><span class=\"hljs-params\">(self, *args, **kwargs)</span>:</span>\n        context = {}\n        self.response.write(template.render(os.path.join(TEMPLATE_DIR, <span class=\"hljs-string\">'index.html'</span>), context))\n</div></pre>\n<p>That's about it!  Moving on to method number two...</p>\n<h4>2. Google App Engine Flex</h4>\n<p>Oooooh &quot;App Engine Flex&quot;!  Sounds new and shiny and perfect and awesome.  It was, at first.</p>\n<p>I had built a very small SPA using Vue.js called <a href=\"https://scrobblin-friends.appspot.com/\">Scrobblin' With Friends</a> as an attempt to bring Last.fm back into my life.  It's a simple app that allows you to enter your Last.fm username, and it shows you what all your friends are listening to.  It's a tiny app, with absolutely zero backend.  All it does is take in a username, hit an API every ~10 seconds, and display some data.  It definitely didn't make sense to bootstrap an App Engine project.</p>\n<p>Then I discovered Flex, and found out they have a Node.js environment.  It was extremely easy to set up:</p>\n<ol>\n<li><code>app.yaml</code> with 2 lines:  <code>runtime: nodejs</code> and <code>env: flex</code></li>\n<li><code>package.json</code> with a <code>start</code> script</li>\n<li>A simple express server</li>\n</ol>\n<pre class=\"hljs\"><div class=\"code\"><span class=\"hljs-comment\">// server.js</span>\n<span class=\"hljs-keyword\">var</span> express = <span class=\"hljs-built_in\">require</span>(<span class=\"hljs-string\">'express'</span>);\n<span class=\"hljs-keyword\">var</span> app = express();\n\n<span class=\"hljs-keyword\">const</span> PORT = process.env.PORT || <span class=\"hljs-number\">8080</span>;\n\napp.get(<span class=\"hljs-string\">'/static/*'</span>, <span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span>(<span class=\"hljs-params\">request, response</span>)</span>{\n     <span class=\"hljs-built_in\">console</span>.log(<span class=\"hljs-string\">'static file request : '</span> + request.params[<span class=\"hljs-number\">0</span>]);\n     response.sendFile( __dirname + <span class=\"hljs-string\">'/static/'</span> + request.params[<span class=\"hljs-number\">0</span>]);\n});\n\napp.get(<span class=\"hljs-string\">'*'</span>, <span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span>(<span class=\"hljs-params\">request, response</span>) </span>{\n    response.sendFile(__dirname + <span class=\"hljs-string\">'/index.html'</span>);\n});\n\napp.listen(PORT);\n</div></pre>\n<p>I finished the setup very quickly, deployed it, and life was good.  That is, until I got my bill for the month.</p>\n<p>One of the big differences you notice between the Standard App Engine environment, and the Flex environment, is that your project never completely spins down.  On Standard, if nobody is hitting your site, you don't pay.  Even if you're getting a little bit of traffic, they're quite generous with the free tier.  Unbeknownst to me, &quot;free tier&quot; isn't a thing on Flex.  I racked up about $30 in a couple days before I noticed.</p>\n<p>I could see Flex being a viable option if you have a project you were willing to pay big bucks to host.  This was not one of those projects.  So back to App Engine Standard I went.</p>\n<h4>3. now</h4>\n<p>If you haven't heard of <a href=\"https://zeit.co/now\">now</a>, go check it out now (heh).  It is seriously cool.  After installing it on your computer, just type <code>now</code> on your command line, and next thing you know, your application is on the web.</p>\n<p>The only requirement is that you have a <code>package.json</code> with a <code>start</code> script.  <code>now</code> takes care of the rest.</p>\n<p>As cool as the deployment process is, there were some things I didn't like about it.</p>\n<ol>\n<li>The free tier gives you these janky URLs for your project, for example: coolproject-glkqdjsslm.now.sh.  I usually don't mind having strange URLs when I'm not paying for hosting, but the issue is that every time you deploy, you get a new URL.  You basically can't host anything that other people are going to use for free</li>\n<li>Their cheapest paid tier, which gets you 1000 deploys/month (instead of 20), private codebases, and more is $14.99/mo.  Much more than I was willing to pay for silly side projects.</li>\n</ol>\n<h4>4. GitHub Pages</h4>\n<p>Finally.  You sat through the excruciating minutia of my SPA hosting experiences.  We have finally arrived at the point of the article, which is to outline my experiences hosting an SPA on GitHub Pages.</p>\n<p>It seems like the logical place to serve a SPA from, right?  You've got free hosting.  Deploys are a breeze.  Free SSL.  Plus your code is probably going to be kept on GitHub anyway!</p>\n<p>I had recently decided that it was time to replace my personal website, which was previously a free Wordpress site that I put zero effort into (and I eventually password protected it because I was embarrassed by it).  My new site, which you are currently looking at, is written in Vue.js with no backend, so I could really choose any provider I wanted.  App Engine (both Standard and Flex) were overkill and/or too expensive, <code>now</code> was also unfeasible because of the reasons I stated above.  So here we are.  GitHub Pages is the chosen one.</p>\n<p>It is not without it's faults.  Of course, there are a few quirks here and there that you might need to work around, but I haven't come across any blockers, and I'm sure you wouldn't either.  That said, if GitHub Pages were a paid service, some of the problems I encountered would have made me throw in the towel and switch to something else.  Since it's free, I was willing to put in the time to work around the issues.</p>\n<p>I'd like to walk through how the whole thing works, including any &quot;gotchas&quot; that I encountered along the way.</p>\n<h3>Part 1: What is GitHub Pages?</h3>\n<p>First off, <a href=\"https://pages.github.com/\">go here</a>.  It has all the basics of Pages, and if you want more detailed documentation you can click &quot;Pages Help&quot; in the top-right corner.</p>\n<p>There are two types of Pages.  The first is an Organization/Personal page.  This lives in a repo called <code>&lt;YOUR_USERNAME&gt;.github.io</code>, has a URL that matches the repo name, and is meant to be a general landing page for a person or organization (duh).  The code for the site lives in this project.</p>\n<p>The other kind, a Project page, most commonly lives in the <code>gh-pages</code> branch, or in a <code>/docs</code> folder on <code>master</code>.  Then, you access the page from <code>&lt;YOUR_USERNAME&gt;.github.io/&lt;PROJECT_NAME&gt;</code>.  It allows you to keep all the code in one place.  Pretty handy, I think.</p>\n<p>For the purposes of this post, I will be talking about Personal pages.</p>\n<h3>Part 2: What should my repo look like?</h3>\n<p>This was the first problem I came across.  A personal site is served from the <code>master</code> branch.  That meant I couldn't just create a project using the Vue CLI, commit everything to master, and expect it to work.</p>\n<p>After doing some research online, I settled on a plan.  I created a <code>sources</code> branch that houses the source files.  Then, on my computer, I built the project, initialized the generated <code>dist</code> directory as a git repo, set it to track <code>master</code>, then committed and pushed.  It worked pretty well, but it was largely manual.  Plus if I ever re-cloned the project or used another computer, I'd have to set up the dist repo again.  Annoying.</p>\n<h3>Part 3: Automating Deployments</h3>\n<p>I was not about to settle for the process laid out above, so I figured I could devise a script that could do that for me, then just set up Travis CI to run it.  There are quite a few scripts floating around the internet that do exactly that.  I played with one for a while, tweaking it to work for my project, when I found out that Travis actually has a GitHub Pages deployment provider.  A couple commits later, I ended up with a pretty simple <code>.travis.yml</code> that took care of everything for me.</p>\n<pre class=\"hljs\"><div class=\"code\"><span class=\"hljs-attr\">language:</span> <span class=\"hljs-string\">node_js</span>\n<span class=\"hljs-attr\">node_js:</span>\n<span class=\"hljs-bullet\">  -</span> <span class=\"hljs-string\">\"node\"</span>\n<span class=\"hljs-attr\">script:</span>\n<span class=\"hljs-bullet\">  -</span> <span class=\"hljs-string\">yarn</span> <span class=\"hljs-string\">run</span> <span class=\"hljs-string\">lint</span>\n<span class=\"hljs-bullet\">  -</span> <span class=\"hljs-string\">yarn</span> <span class=\"hljs-string\">run</span> <span class=\"hljs-string\">build</span>\n<span class=\"hljs-attr\">deploy:</span>\n<span class=\"hljs-attr\">  provider:</span> <span class=\"hljs-string\">pages</span>\n<span class=\"hljs-attr\">  skip_cleanup:</span> <span class=\"hljs-literal\">true</span>\n<span class=\"hljs-attr\">  github_token:</span> <span class=\"hljs-string\">$GITHUB_TOKEN</span>\n<span class=\"hljs-attr\">  local_dir:</span> <span class=\"hljs-string\">dist</span>\n<span class=\"hljs-attr\">  target_branch:</span> <span class=\"hljs-string\">master</span>\n<span class=\"hljs-attr\">  on:</span>\n<span class=\"hljs-attr\">    branch:</span> <span class=\"hljs-string\">sources</span>\n</div></pre>\n<p>It basically just runs my lint and build, then if the branch is <code>sources</code>, force-push the <code>dist</code> directory to <code>master</code>.  For more info on this, check out the <a href=\"https://docs.travis-ci.com/user/deployment/pages/\">Travis CI docs</a>.</p>\n<h3>Part 4: Routing</h3>\n<p>It was very smooth sailing for a while.  I was developing locally very quickly using the webpack dev server, complete with hot module reloading.  When I wanted to deploy, I just pushed up my changes and in a minute or so they were live.  I had no problems setting up the Vue router at all.</p>\n<p>As it turns out, when I was using the live site I was always starting from the root of the site.  I never tried to go directly to <code>/projects</code> or any other page.  That's when I hit a snag.  Trying to go directly to a route in my app resulted in a 404 page.</p>\n<p>I definitely wasn't shocked when I discovered this.  It makes total sense, since GitHub doesn't know about the Vue app's routes.  All it knows is there is an <code>index.html</code> file in the repo that it can route to.</p>\n<p>I attempted two different solutions to this problem.  Depending on your requirements, one may be more appropriate than the other.</p>\n<h4>Attempt 1: Hacking the 404 page.</h4>\n<p>I found another person online that was trying to solve the same problem as me.  You can see his blog post <a href=\"http://www.backalleycoder.com/2016/05/13/sghpa-the-single-page-app-hack-for-github-pages/\">here</a>.  I'll admit it's quite a janky solution to the problem but it was also simple to implement.  Until GitHub comes up with a better solution, this is a pretty decent workaround.</p>\n<p>This solution is based on the fact that you can use a custom 404 page on GH Pages.  All you have to do is add a <code>404.html</code> page to your repo.  Following the link above, I created <code>404.html</code> with the following lines in <code>&lt;head&gt;</code>:</p>\n<pre class=\"hljs\"><div class=\"code\"><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">script</span>&gt;</span><span class=\"undefined\">\n  sessionStorage.redirect = location.href;\n</span><span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">script</span>&gt;</span>\n<span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">meta</span> <span class=\"hljs-attr\">http-equiv</span>=<span class=\"hljs-string\">\"refresh\"</span> <span class=\"hljs-attr\">content</span>=<span class=\"hljs-string\">\"0;URL='/'\"</span>&gt;</span><span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">meta</span>&gt;</span>\n</div></pre>\n<p>Basically all this does is save the requested URL in your browser's storage, and boot you back to the root of the site.  Obviously, that's not enough.  If someone click a link to this blog post, I wouldn't want them being sent to the homepage.  The next step was just as simple.  I basically added another script to <code>index.html</code> to check for that saved URL, and redirect to it if it exists.</p>\n<pre class=\"hljs\"><div class=\"code\"><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">script</span>&gt;</span><span class=\"actionscript\">\n  (<span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span><span class=\"hljs-params\">()</span></span>{\n    <span class=\"hljs-keyword\">var</span> redirect = sessionStorage.redirect;\n    <span class=\"hljs-keyword\">delete</span> sessionStorage.redirect;\n    <span class=\"hljs-keyword\">if</span> (redirect &amp;&amp; redirect != location.href) {\n      history.replaceState(<span class=\"hljs-literal\">null</span>, <span class=\"hljs-literal\">null</span>, redirect);\n    }\n  })();\n</span><span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">script</span>&gt;</span>\n</div></pre>\n<p>This worked surprisingly well.  However, once I decided to implement Open Graph and Twitter meta tags in my blog posts, it fell apart due to the scrapers not following the redirects.  However, if you have a simple site that doesn't require meta tags, then this just might work for you.</p>\n<h4>Attempt 2: Pre-rendering a Static Site</h4>\n<p>There were two issues with the above router hack in terms of serving meta tags:</p>\n<ol>\n<li>When Facebook, Slack, Twitter, or any other site with rich media cards would scan my links, they would land on the 404 page as expected.  However, they would not get redirected to the proper page by the method I shared above.  This is because GitHub Pages returned a 404 status code for the 404 page, which told the scrapers to give up.</li>\n<li>Even if I didn't use the 404 page hack, my meta tags were generated on the fly in my Vue app.  Since the scraper downloads the page without executing any javascript, it would not find the meta tags.</li>\n</ol>\n<p>So, the two obvious solutions are server-side rendering or pre-rending.  I didn't want to go down the path of server-side rendering, since I don't want to host a server for this website.  So, pre-rendering it is!</p>\n<p>Since I'm using Webpack, I thought it made sense to add a webpack plugin to generate static HTML from my site.  I found a couple prebuilt plugins to do this, which both leveraged PhantomJS to render the site.  For some reason, I could not get it to render any actual content in my site.  I wasted a ton of time trying to get it working, with no success.</p>\n<p>Frustrated with that, I decided to write my own plugin.  Thus, <a href=\"https://github.com/evansalter/webpack-static-site-generator\">webpack-static-site-generator</a> was born (great name, I know).  With that plugin in place, every time I built my site for production I would get static HTML files for all of my pages.  I could still use the Webpack dev server when developing locally without building, which was a huge plus.</p>\n<p>So, if you have any trouble hosting your SPA on any hosting provider, you might want to check out pre-rendering.</p>\n<h3>Part 5: Custom Domain</h3>\n<p>If you've made it this far, you should be done with the &quot;gotchas&quot; of hosting a single-page app on GH Pages.  Setting up a custom domain was very easy.  There were just a couple steps:</p>\n<ol>\n<li>Create a file called <code>CNAME</code> in your repo, which contains a single line with the domain you want to use (i.e. <code>www.evansalter.com</code>).  Make sure this file get commited to your master branch when the build runs.</li>\n<li>Set up a <code>CNAME</code> record with your domain registrar that points to your <code>github.io</code> address (i.e. <code>evansalter.github.io</code>)</li>\n</ol>\n<p>There are some slightly different instructions on GitHub's support pages if you want to use an apex domain (no www), but it shouldn't be too much work.</p>\n<p>There was one slight problem I discovered.  There is NO SSL support when you're using a custom domain with GH Pages.  I was a tad annoyed at this, but seeing as my site has no real reason to be on HTTPS, I got over it.</p>\n<h3>Final Thoughts</h3>\n<p>I very excited about using GitHub Pages to host a single-page app when I first started this site.  However, it wasn't quite as magical as it seemed.  I faced many problems along the way.  Some of them had simple solutions, while others were a little more complicated.  However, I'm still happy with how it worked out in the end.  The simplicity of Pages is what makes it great, and I would recommend that everybody at least considers it for their next project.</p>\n</section>\n";

