module.exports = "<section><p><img src=\"" + __webpack_require__(23) + "\" alt=\"\"></p>\n<p><sup><em>The finished product</em></sup></p>\n<p>I recently attended a 50th birthday party for my partner's Aunt and Uncle. When planning, the idea of a photo booth came up. &quot;I have a nice camera&quot;, I thought, deciding it shouldn't be too difficult to set up. I set out exploring different software options to help me with this. The criteria was as follows:</p>\n<ol>\n<li>Tethers to cameras to allow an operator-free photo booth experience</li>\n<li>Takes 3-4 pictures at a time</li>\n<li>(optional) Accepts email addresses to send the pictures out</li>\n<li>(optional) Creates photo strips or collages of each session</li>\n<li>Is free or cheap</li>\n<li>Works with Sony cameras</li>\n<li>Runs on MacOS</li>\n</ol>\n<p>Unfortunately, I couldn't find anything that met all my criteria. I did find some free or relatively cheap options, but they either didn't work with my Sony a6000 or required Windows to run. Most of them were just too expensive for my uses.</p>\n<p>Through this research, I discovered that my camera actually has <a href=\"https://developer.sony.com/file/download/sony-camera-remote-api-beta-sdk-2/\">an API</a> you can interact with over the WiFi connection! So, with 3 days before the event, I put on my developer hat and set out to build my own solution. Without further ado, let's see what I did.</p>\n<h4>The Requirements</h4>\n<p>When I first started this project, the plan was to create a simple UI that shows a live view of the camera, has a button that starts a 3-second countdown, then takes 4 pictures with a second or 2 in between.</p>\n<p>After playing with the API and various libraries on different platforms, I realized it was too complex to support a live view of the camera. It is implemented as a streaming HTTP request that you need to parse in chunks to decode the image. While doable, it wasn't necessary so I decided to focus my efforts on taking and displaying the pictures.</p>\n<p>That leaves us with the following requirements:</p>\n<ol>\n<li>The app has a button to start the capture that users can press</li>\n<li>There is a 3 second countdown before taking any pictures</li>\n<li>4 pictures are taken with a short delay between each</li>\n<li>The photos are displayed in the screen after each session</li>\n</ol>\n<h4>The UI</h4>\n<p>To set this up quickly, I decided to make a quick Vue app. The app is dead simple, consisting of a single component that does everything.</p>\n<p>The template just contains a grid of pictures and a button to start the countdown</p>\n<pre class=\"hljs\"><div class=\"code\"><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">div</span> <span class=\"hljs-attr\">id</span>=<span class=\"hljs-string\">\"app\"</span>&gt;</span>\n  <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">div</span> <span class=\"hljs-attr\">v-if</span>=<span class=\"hljs-string\">\"!cam.connected\"</span>&gt;</span>\n    Waiting for camera...\n  <span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">div</span>&gt;</span>\n  <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">div</span> <span class=\"hljs-attr\">v-if</span>=<span class=\"hljs-string\">\"cam.connected\"</span>&gt;</span>\n    <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">div</span> <span class=\"hljs-attr\">class</span>=<span class=\"hljs-string\">\"pics\"</span>&gt;</span>\n      <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">img</span> <span class=\"hljs-attr\">class</span>=<span class=\"hljs-string\">\"pic\"</span> <span class=\"hljs-attr\">v-for</span>=<span class=\"hljs-string\">\"pic in pictures\"</span> <span class=\"hljs-attr\">:key</span>=<span class=\"hljs-string\">\"pic\"</span> <span class=\"hljs-attr\">:src</span>=<span class=\"hljs-string\">\"pic\"</span>/&gt;</span>\n    <span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">div</span>&gt;</span>\n    <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">div</span> <span class=\"hljs-attr\">class</span>=<span class=\"hljs-string\">\"button-div\"</span> <span class=\"hljs-attr\">v-if</span>=<span class=\"hljs-string\">\"!active || countdown !== 0\"</span>&gt;</span>\n      <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">button</span> <span class=\"hljs-attr\">class</span>=<span class=\"hljs-string\">\"button\"</span> @<span class=\"hljs-attr\">click</span>=<span class=\"hljs-string\">\"startPics\"</span> <span class=\"hljs-attr\">:disabled</span>=<span class=\"hljs-string\">\"active\"</span>&gt;</span>\n        <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">span</span> <span class=\"hljs-attr\">v-if</span>=<span class=\"hljs-string\">\"countdown === 0\"</span>&gt;</span><span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">span</span>&gt;</span>\n        <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">span</span> <span class=\"hljs-attr\">v-if</span>=<span class=\"hljs-string\">\"countdown !== 0\"</span>&gt;</span>{{ countdown }}<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">span</span>&gt;</span>\n      <span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">button</span>&gt;</span>\n    <span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">div</span>&gt;</span>\n  <span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">div</span>&gt;</span>\n<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">div</span>&gt;</span>\n</div></pre>\n<p><img src=\"" + __webpack_require__(186) + "\" alt=\"\"></p>\n<p>After about 10 seconds, the app resets and allows another set of pictures to be taken.</p>\n<p>The Javascript for this was pretty straightforward as well. Given more time, I could have improved the code quality, but this worked very well:</p>\n<pre class=\"hljs\"><div class=\"code\"><span class=\"hljs-keyword\">import</span> SonyCamera <span class=\"hljs-keyword\">from</span> <span class=\"hljs-string\">'sony-camera'</span>\n\n<span class=\"hljs-keyword\">export</span> <span class=\"hljs-keyword\">default</span> {\n  <span class=\"hljs-attr\">name</span>: <span class=\"hljs-string\">'App'</span>,\n  <span class=\"hljs-attr\">components</span>: {},\n  <span class=\"hljs-attr\">data</span>: <span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span> (<span class=\"hljs-params\"></span>) </span>{\n    <span class=\"hljs-keyword\">return</span> {\n      <span class=\"hljs-attr\">cam</span>: <span class=\"hljs-keyword\">new</span> SonyCamera(<span class=\"hljs-string\">'localhost'</span>, <span class=\"hljs-number\">3000</span>),\n      <span class=\"hljs-attr\">liveViewFrame</span>: <span class=\"hljs-string\">''</span>,\n      <span class=\"hljs-attr\">pictures</span>: [],\n      <span class=\"hljs-attr\">countdown</span>: <span class=\"hljs-number\">0</span>,\n      <span class=\"hljs-attr\">active</span>: <span class=\"hljs-literal\">false</span>\n    }\n  },\n  <span class=\"hljs-attr\">methods</span>: {\n    <span class=\"hljs-attr\">startPics</span>: <span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span> (<span class=\"hljs-params\">event</span>) </span>{\n      <span class=\"hljs-keyword\">this</span>.active = <span class=\"hljs-literal\">true</span>\n      <span class=\"hljs-keyword\">this</span>.countdown = <span class=\"hljs-number\">3</span>\n      <span class=\"hljs-keyword\">var</span> countID = setInterval(<span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span> (<span class=\"hljs-params\"></span>) </span>{\n        <span class=\"hljs-keyword\">this</span>.countdown -= <span class=\"hljs-number\">1</span>\n        <span class=\"hljs-keyword\">if</span> (<span class=\"hljs-keyword\">this</span>.countdown !== <span class=\"hljs-number\">0</span>) {\n          <span class=\"hljs-keyword\">return</span>\n        }\n        clearInterval(countID)\n        <span class=\"hljs-keyword\">this</span>.capture(<span class=\"hljs-number\">4</span>)\n      }.bind(<span class=\"hljs-keyword\">this</span>), <span class=\"hljs-number\">1000</span>)\n    },\n    <span class=\"hljs-attr\">capture</span>: <span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span> (<span class=\"hljs-params\">numToTake</span>) </span>{\n      <span class=\"hljs-keyword\">this</span>.cam.capture(<span class=\"hljs-literal\">true</span>, <span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span> (<span class=\"hljs-params\">x, name, imageData</span>) </span>{\n        <span class=\"hljs-keyword\">if</span> (name) {\n          <span class=\"hljs-built_in\">console</span>.log(name)\n          <span class=\"hljs-keyword\">this</span>.pictures.push(<span class=\"hljs-string\">`http://localhost:3000/postview/<span class=\"hljs-subst\">${name}</span>`</span>)\n          <span class=\"hljs-keyword\">if</span> (numToTake &gt; <span class=\"hljs-number\">1</span>) {\n            setTimeout(<span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span> (<span class=\"hljs-params\"></span>) </span>{ <span class=\"hljs-keyword\">this</span>.capture(numToTake - <span class=\"hljs-number\">1</span>) }.bind(<span class=\"hljs-keyword\">this</span>), <span class=\"hljs-number\">2000</span>)\n          } <span class=\"hljs-keyword\">else</span> {\n            setTimeout(<span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span> (<span class=\"hljs-params\"></span>) </span>{\n              <span class=\"hljs-keyword\">this</span>.pictures = []\n              <span class=\"hljs-keyword\">this</span>.active = <span class=\"hljs-literal\">false</span>\n            }.bind(<span class=\"hljs-keyword\">this</span>), <span class=\"hljs-number\">10000</span>)\n          }\n        }\n      }.bind(<span class=\"hljs-keyword\">this</span>))\n    }\n  },\n  <span class=\"hljs-attr\">mounted</span>: <span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span> (<span class=\"hljs-params\"></span>) </span>{\n    <span class=\"hljs-keyword\">this</span>.cam.connect(<span class=\"hljs-built_in\">console</span>.log)\n  }\n}\n</div></pre>\n<p>I used the <a href=\"https://www.npmjs.com/package/sony-camera\"><code>sony-camera</code></a> NPM package to interface with the camera. The only methods I needed for this was <code>connect()</code> and <code>capture()</code>. When the button is clicked and the countdown has elapsed, I recursively call <code>capture()</code> using <code>setTimeout()</code> to space out the images. The callback to <code>capture()</code> gets the URL of the image to display and appends it to a list of URLs, which the template binds to the <code>src</code> attribute of <code>img</code> elements.</p>\n<h4>The Server</h4>\n<p>You may have noticed that I am initializing the <code>sony-camera</code> library with an address of <code>localhost:3000</code>. The reason for this is that my requests to the camera were being blocked by CORS, as the camera doesn't set any <code>Access-Control-Allow-Origin</code> header. So, I built a simple node.js proxy server to route my requests through.</p>\n<pre class=\"hljs\"><div class=\"code\"><span class=\"hljs-keyword\">var</span> http = <span class=\"hljs-built_in\">require</span>(<span class=\"hljs-string\">'http'</span>);\n\nhttp.createServer(onRequest).listen(<span class=\"hljs-number\">3000</span>);\n\n<span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span> <span class=\"hljs-title\">onRequest</span>(<span class=\"hljs-params\">client_req, client_res</span>) </span>{\n  <span class=\"hljs-built_in\">console</span>.log(<span class=\"hljs-string\">'serve: '</span> + client_req.url);\n\n  <span class=\"hljs-keyword\">var</span> options = {\n    <span class=\"hljs-attr\">hostname</span>: <span class=\"hljs-string\">'192.168.122.1'</span>,\n    <span class=\"hljs-attr\">port</span>: <span class=\"hljs-number\">8080</span>,\n    <span class=\"hljs-attr\">path</span>: client_req.url,\n    <span class=\"hljs-attr\">method</span>: client_req.method,\n    <span class=\"hljs-attr\">headers</span>: client_req.headers\n  };\n\n  <span class=\"hljs-keyword\">var</span> proxy = http.request(options, <span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span> (<span class=\"hljs-params\">res</span>) </span>{\n    res.headers[<span class=\"hljs-string\">'access-control-allow-origin'</span>] = <span class=\"hljs-string\">'*'</span>\n    client_res.writeHead(res.statusCode, res.headers)\n    res.pipe(client_res, {\n      <span class=\"hljs-attr\">end</span>: <span class=\"hljs-literal\">true</span>\n    }).on(<span class=\"hljs-string\">'error'</span>, <span class=\"hljs-built_in\">console</span>.log);\n  });\n\n  client_req.pipe(proxy, {\n    <span class=\"hljs-attr\">end</span>: <span class=\"hljs-literal\">true</span>\n  }).on(<span class=\"hljs-string\">'error'</span>, <span class=\"hljs-built_in\">console</span>.log);\n}\n</div></pre>\n<p>All of the code is <a href=\"https://gist.github.com/evansalter/694487c86f6c6d79892e418840ad6c9a\">here</a>.</p>\n<h4>Putting it all Together</h4>\n<p>At the party, we set up a backdrop using table cloths and other decorations. We picked up some novelty glasses that people can wear for the pictures, and left a notepad for people to write down their email addresses on.</p>\n<p>In front of the backdrop, I set up a small table with a tripod and my laptop. I set up my camera on the tripod with a speedlight attached, switched to manual mode and played with the settings until it looked right (I cheaped out on the speedlights, so they don't have TTL metering that works with my Sony camera. This requires some trial-and-error). I also brought my battery charger with 2 extra camera batteries, since the Sony a6000 doesn't allow you to charge via USB while using the camera.</p>\n<p>Finally, I turned on the computer, enabled <a href=\"http://lightheadsw.com/caffeine/\">Caffeine</a> so the screensaver wouldn't start, and connected the camera:</p>\n<ol>\n<li>Turn on the camera</li>\n<li>Start the Smart Remote Control app on the camera</li>\n<li>Connect the computer to the Camera's WiFi network</li>\n<li>Start the proxy server</li>\n<li>Start the app server</li>\n<li>Open the browser to the app</li>\n</ol>\n<p>And that's it! The program ran smoothly for the majority of the night. The camera battery died once, so I just swapped that out and re-connected. I also discovered the speedlight turns off automatically after not firing for some time, so I ended up with a couple batches of dark pictures.</p>\n<h4>Other Specs</h4>\n<p><strong>Camera</strong>: Sony a6000<br>\n<strong>Lens</strong>: Sony E 18-105 f/4 G<br>\n<strong>Focal Length</strong>: 20mm<br>\n<strong>Aperature</strong>: f/7.1<br>\n<strong>Shutter Speed</strong>: 1/100 s<br>\n<strong>ISO</strong> 640</p>\n<p>Ideally, I would have shot with my FE 50mm, but I just didn't have enough space for that. I chose a ~f/7 aperature to ensure I got everybody in focus, and I tried to keep the speedlight power low to avoid the photo booth being too distracting for the rest of the guests.</p>\n<h4>Summary</h4>\n<p>I was extremely happy with the result of this project. Being able to use what I already had to pull this off felt great. The photo booth got lots of use, with 232 photos being taken throughout the night. People had a lot of fun with it, and it gave the hosts some great mementos from the night. Had I given myself some more time, I probably could have made a more robust app, but it really wasn't necessary.</p>\n<p>Check out Part 2, <a href=\"/blog/processing-232-photo-booth-photos\">Processing 232 Photo Booth Photos</a>, to learn about how I processed the images.</p>\n</section>\n";

